import React, { useEffect, useRef } from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import styled from 'styled-components';
import { srConfig } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';
import ReactFlow from 'react-flow-renderer';

const StyledAboutSection = styled.section`
  max-width: 900px;

  .inner {
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-gap: 50px;

    @media (max-width: 768px) {
      display: block;
    }
  }
`;

const StyledText = styled.div`
  ul.skills-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(140px, 200px));
    grid-gap: 0 10px;
    padding: 0;
    margin: 20px 0 0 0;
    overflow: hidden;
    list-style: none;

    li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 20px;
      font-family: var(--font-mono);
      font-size: var(--fz-xs);

      &:before {
        content: '▹';
        position: absolute;
        left: 0;
        color: var(--green);
        font-size: var(--fz-sm);
        line-height: 12px;
      }
    }
  }
`;

const StyledDiagramContainer = styled.div`
  position: relative;
  width: 100%;
  height: 400px; /* Set appropriate height */
  background-color: var(--light-navy);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
`;

const About = () => {
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }
    sr.reveal(revealContainer.current, srConfig());
  }, []);

  // Nodes and Edges
  const elements = [
    { id: '1', type: 'input', data: { label: 'Custom Software Solutions' }, position: { x: 50, y: 0 } },
    { id: '2', data: { label: 'Data Strategy Development' }, position: { x: -50, y: 150 } },
    { id: '3', data: { label: 'Data Processing' }, position: { x: 150, y: 150 } },
    { id: '4', type: 'output', data: { label: 'Machine Learning Integration' }, position: { x: 50, y: 300 } },
    { id: 'e1-2', source: '1', target: '2', animated: true },
    { id: 'e1-3', source: '1', target: '3', animated: true },
    { id: 'e2-4', source: '2', target: '4', animated: true },
    { id: 'e3-4', source: '3', target: '4', animated: true },
  ];

  return (
    <StyledAboutSection id="about" ref={revealContainer}>
      <h2 className="numbered-heading">My Expertise</h2>
      <div className="inner">
        <StyledText>
          <div>
            <p>
              I am your partner in harnessing the power of data. I believe that data is
              more than just numbers; it's a valuable resource that can drive impactful outcomes.
            </p>
            <p>
              I offer custom software solutions tailored to your data needs, strategy development,
              and expertise in data processing.
            </p>
          </div>
          <ul className="skills-list">
            <li>Custom software solutions for data analysis, visualization, and AI-powered tools.</li>
            <li>Data strategy development, technology identification, and workflow optimization.</li>
            <li>Data processing expertise including cleaning, organizing, and potential insights.</li>
            <li>Machine learning API integration for seamless enhancement of web applications.</li>
          </ul>
        </StyledText>
        <StyledDiagramContainer>
          <ReactFlow elements={elements} style={{ width: '100%', height: '100%' }} fitView />
        </StyledDiagramContainer>
      </div>
    </StyledAboutSection>
  );
};

export default About;
